import Vue from 'vue'
import { Toast } from 'vant';

Vue.use(Toast);
//
export function checkphone(phone) {
  const reg=/^1[3456789]\d{9}$/;
  if (!reg.test(phone)) {
    Toast("");
    return false;
  } else {
    return true;
  }
}
//
export function checkphoneandtelephone(phone){
  var isPhone = /^1[3456789]\d{9}$/;
  var isMob=/^((\+?86)|(\(\+86\)))?(13[012356789][0-9]{8}|15[012356789][0-9]{8}|18[02356789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/;
  if(isMob.test(phone)||isPhone.test(phone)){
    return true;
  }  else{
    Toast("");
    return false;
  }
}
//
export function checkidentificationcard(identificationcard){
  const reg = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
  if (!reg.test(identificationcard)) {
    Toast("");
    return false;
  } else {
    return true;
  }
}
//
export function checkempty(val,toast) {
  if (val === ""  || val === false || val === null || val.length == 0) {
    if(toast){
      Toast(toast);
    }
    return false;
  } else {
    return true;
  }
}
/**
 * ja
 * @param 2019.1.18
 * @returns {string}
 */
/**
        
        file()
        objDiv
        photoCompress()
        quality .
         */
export function photoCompress(file,objDiv,quality){
  console.log(quality);
  var ready=new FileReader();
  /*BlobFile. ,readyStateDONE,onloadend,.,resultdata: URL.*/
  ready.readAsDataURL(file);
  ready.onload=function(){
    var re=this.result;
    canvasDataURL(re,objDiv,quality)
  }
}
 function canvasDataURL(path, callback,quality){
   console.log(quality);
  var img = new Image();
  img.src = path;
  img.onload = function(){
    var that = this;
    // 
    var w = that.width,
      h = that.height,
      scale = w / h;
    w =  w;
    h =  (w / scale);
    // console.log('w:'+w,'h:'+h)
    // var quality = quality;  // 0.7
    //canvas
    var canvas = document.createElement('canvas');
    var ctx = canvas.getContext('2d');
    // 
    var anw = document.createAttribute("width");
    anw.nodeValue = w;
    var anh = document.createAttribute("height");
    anh.nodeValue = h;
    canvas.setAttributeNode(anw);
    canvas.setAttributeNode(anh);
    ctx.drawImage(that, 0, 0, w, h);
    // 
    // if(obj.quality && obj.quality <= 1 && obj.quality > 0){
    //   quality = obj.quality;
    // }
    // quality
    var base64 = canvas.toDataURL('image/jpeg', quality);
    // base64
    callback(base64);
  }
}

//base64  
export function dataURLtoFile(dataurl, filename) {
  let arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
  while(n--){
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, {type:mime});
}


//  vue
//2018-04-23
export  function timestampconvert(time) {
  time = new Date( time*1000 ) ;
  return time.getFullYear() + "-" + (time.getMonth() + 1) + "-" + time.getDate();
}
//2018-04-23 11:08
export  function timestampconvertcolon(time) {
  time = new Date( time*1000 ) ;
  return time.getFullYear() + "-" + (time.getMonth() + 1) + "-" + time.getDate() + " "+time.getHours() + ":" +time.getMinutes();
}
// 138****9999
export function phonehide(phone) {
  return phone.slice(0,3)+'****'+phone.slice(7,11)
}
// 
export function formatMoney(money) {
  if (money && money != null) {
    money = String(money);
    var left = money.split('.')[0],
      right = money.split('.')[1];
    right = right ? (right.length >= 2 ? '.' + right.substr(0, 2) : '.' + right + '0') : '.00';
    var temp = left.split('').reverse().join('').match(/(\d{1,3})/g);
    return (Number(money) < 0 ? "-" : "") + temp.join(',').split('').reverse().join('') + right;
  } else if (money === 0) {
    //===money0,ifboolean===
    return '0.00';
  } else {
    return "";
  }
}
const tool = {
  timestampconvert,
  timestampconvertcolon,
  phonehide,
  formatMoney,
};
Object.keys(tool).forEach(key=>{
  Vue.filter(key,tool[key]);
});



